import React from 'react'

const Form = ({ onInputChange, onButtonSubmit }) => {
  return (
    <div className='center mt5 pa3'>
      <input onChange={onInputChange} className='f5 br2 input shadow-1' type="url" placeholder='Submit the image url here'/>
      <button onClick={onButtonSubmit} className='f4 btn shadow-1 grow ma2'>Submit</button>
    </div>
  )
}
export default Form


