import React, { Component } from 'react'
import './App.css'
import Heading from './components/heading'
import Logo from './components/logo'
import Form from './components/form'
import Clarifai from 'clarifai'
import Output from './components/output'
import Image from './components/image'



const app = new Clarifai.App({
  apiKey: 'fabb876464dc42bd8cf98a3b12dbb519'
 });
 


class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      url: '',
      image: ''
    }
  }
 


  onInputChange = (e)=>{
    this.setState({
      url: e.target.value
    })
    document.getElementById('result').classList.add('none')
    document.getElementById('colorResult').classList.add('none')
    document.getElementById('image').classList.add('none')
  }

 onButtonSubmit = ()=>{
  document.getElementById('result').innerHTML = '<p>Loading...</p>'
  document.getElementById('colorResult').innerHTML = '<p>Loading...</p>'
  document.getElementById('result').classList.remove('none')
  document.getElementById('colorResult').classList.remove('none')
// apparel model
  app.models.predict(Clarifai.APPAREL_MODEL, this.state.url).then(
    function(response) {
  
      document.getElementById('image').classList.remove('none')
      const result = response.outputs[0].data.concepts
      const result1 = result[0].name
      const result2 = result[1].name
      const value = result[0].value
      console.log(value)
      if(value > 0.49){
          document.getElementById('result').innerHTML = '<p>This is a.......</p><li>'+ result1 +'</li><li><span> or </span>'+ result2 +'</li>'
      }else{
         document.getElementById('result').innerHTML =  '<p>This is not a apparel.</p>'
      }

    },
    function(err) {
      document.getElementById('result').innerHTML = '<p>Image source is invalid </p>'
    }
  );

  // color modal

  app.models.predict(Clarifai.COLOR_MODEL, this.state.url).then(
    function(response) {
        const color = response.outputs[0].data.colors
        console.log(color)
        const hex1 = color[0].w3c.hex
        const color1 = color[0].w3c.name
        const color2 = color[1].w3c.name
        const hex2 = color[1].w3c.hex

       
            document.getElementById('colorResult').innerHTML = '<p>Major colors in this picture...</p><li>'+ color1 +'<span> (hex</span>:'+ hex1+')</li>'+
            '<li>'+ color2 +'<span> (hex</span>:'+ hex2+')</li>'
        
    },
    function(err) {
      document.getElementById('colorResult').innerHTML = '<li>No color found</li>'
    }
  );


  // general modal

  app.models.predict(Clarifai.FOOD_MODEL, this.state.url).then(
    function(response) {
        console.log(response)
    },
    function(err) {
      
    }
  );



  this.setState({
    image: this.state.url
  })
 }




  render() {
    return (
      <div className="App">
        <Logo/>
        <Heading/>
        <Form onInputChange={this.onInputChange} onButtonSubmit={this.onButtonSubmit}/>
        <Image image={this.state.image} />
        <Output />
      </div>
    );
  }
}

export default App;



