import React from 'react'
import brain from '../img/brain.png'

const Logo = () => {
  return (
    <div className='pa2 logo'>
      <div className='center white pointer'>
         <img src={brain} alt=""/>
         <p>PhotoForensic</p>
      </div>
    </div>
  )
}
export default Logo


